import React from 'react'
// Modal Components
import { Box, Button, Dialog, DialogContent } from '@mui/material'
import ReactPlayer from 'react-player'
import { FaTimes } from 'react-icons/fa'

export default function VideoModal(props) {

    return (
        <div>
            <Dialog
                open={props.open}
                onClose={props.toggle}
                fullWidth={true}
                fullScreen={true}
                sx={{
                    zIndex: 9999,
                    '.MuiDialog-paper': {
                        background: 'transparent',
                        boxShadow: 'none'
                    }
                }}
                disableScrollLock
            >
                <DialogContent sx={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center', overflow: 'hidden' }}>
                    <Box width="95%" height="95%" sx={{ mt: 2, p: 1, backgroundColor: "#FFF" }}>
                        <Button onClick={props.toggle} color='secondary' variant="contained" sx={{ zIndex: 3000, position: 'fixed', top: 5, right: 60 }}>Close <FaTimes /></Button>
                        <ReactPlayer
                            url={props.url}
                            playing={true}
                            width="100%"
                            height="100%"
                            controls={true}
                        />
                    </Box>
                </DialogContent>
            </Dialog>
        </div >
    )
}
